
// TerritoryTimes

import { NextRequest, NextResponse } from "next/server";
import { CustomError, CustomResponse, getCategories, isPassword, passwordError, scheduler, supabase } from "../../props";
import { lostArkApi } from "../route";

export const getTerritoryTimes = async () => {
    let result: any[] = [];
    try {
        const category = await getCategories(`LOA`,`TERRITORY_TIME`);
        const { data:contents, error } = await supabase.from(`contents`).select(`*,profile:profile(*)`).eq(`category`,category);
        for(const content of contents){
            const { data:profiles } = await supabase.from(`profiles`).select(`*`).eq(`type`,content.profile.type).eq(`account`,content.profile.account);
            result.push({
                ...content,
                profiles:profiles,
            })
        }
    }
    catch (error: any) { throw error; }
    console.log(result);
    return result;
}

export async function GET(req: NextRequest) {
    if (!isPassword(req.headers.get("X-User-Password"))) { return passwordError; }
    try {
        const api = await lostArkApi.calendar();
        await getTerritoryTimes();

        return CustomResponse();
    }
    catch (e: any) {
        if (e instanceof CustomError) {
            console.error(`${e.message}`, e.error);
            return NextResponse.json(
                { message: e.message, data: [], },
                { status: e.status },
            );
        }
        return NextResponse.json(
            { message: 'An unexpected error occurred during API processing.', data: [], },
            { status: 500 },
        );
    }
}